            <p class="p7">HELP</p>
            <p class="p8"><a href="">Shipping & Returns</a></p>
            <p class="p8"><a href="">Privacy Policy</a></p>
            <p class="p8"><a href="">FAQ</a></p>
            <p class="p9">SUBSCRIBE</p>
            <div class="formt">
                <label class="label">Enter your email here * </label>   
                <input  type="text" class="input"/>  
                <div class="btn">Subscribe Now</div>
            </div> 
            <div class="footimg">
                <a href=""><img src="../../../assets/picture/icon1.webp" alt=""></a>
                <a href=""><img src="../../../assets/picture/icon2.webp" alt=""></a>  
            </div>
        </div>
        <div class="copyr">Â© 2021 by Commerce Ingredients Inc.</div>

        <div class="toup" v-if="scrollTopStatus" @click="scrollToTop">
          <img src="../../../assets/picture/up1.png" alt="">
      </div>
    </div>
	
</template>

<script>
    import {mapState,mapMutations} from 'vuex'
    export default {
        data() {
			return {
                scrollTopStatus: false,
                cartSize:350,
            }
        },
        computed:mapState({
            active: state => state.active,
            cartStatus: state => state.cartStatus,
            cartList: state => state.cartList,
            totalPrices: state => state.totalPrices,
        }),
        created(){
            this.getCartList();
        },
        mounted(){
            window.addEventListener('scroll',this.handleScroll)
        },
        methods:{
            ...mapMutations(['changeCartStatus','saveCartList','getCartList','deleteCartShop']),
            shutCart(){
                this.changeCartStatus(false);
            },
            cartButton(){
                this.changeCartStatus(true);
            },
            cartClose(){
                this.changeCartStatus(false);
            },
            pushIndex(el){
                localStorage.setItem('pushIndex',el);
                this.$router.push('/')
            },
            deleteshop(id){
                this.deleteCartShop(id)
            },
            reduceCart(item){
                if(item.count>1){
                    this.saveCartList({id:item.id,count:-1});
                }
            },
            addCart(item){
                let {id,name,price} = item;
                this.saveCartList({id,name,price,count:1});
            },
            handleScroll(){
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                
                if(scrollTop > 1000){
                    this.scrollTopStatus = true;
                }else{
                    this.scrollTopStatus = false;
                }
            },
            scrollToTop(){
                document.documentElement.scrollTop = 0;
            }
        },
        destroyed () {
            window.removeEventListener('scroll', this.handleScroll); 
        }
    };
</script>

